*{box-sizing:content-box;margin:0}.sign-up-page{width:100%;height:100vh;font-family:"Jua", sans-serif}.sign-up-page .form-input{display:flex;flex-direction:column;width:35%;justify-content:center;margin:0 auto;margin-top:4rem;background-color:skyblue;padding:3rem;border-radius:1rem}.sign-up-page .form-input h3{font-size:2rem;text-align:center;color:black}.sign-up-page .form-input .sign-up-input{margin-top:2rem;height:3rem;background-color:lightgray;border:1px solid black;border-radius:0.5rem;font-size:1rem;letter-spacing:0.1rem;padding-left:1rem}.sign-up-page .form-input .sign-up-input::-webkit-input-placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-up-page .form-input .sign-up-input:-ms-input-placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-up-page .form-input .sign-up-input::placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-up-page .sign-up-title{display:flex;font-style:italic;padding-top:3rem;font-size:3em;color:tomato;justify-content:center}.sign-up-page .sign-up-title strong{color:blue}.sign-up-page .submit-btn{width:20%;margin:4rem auto 0 auto;padding:1rem;border-radius:1.5rem;border:transparent;background-color:#4285f4;color:white;font-size:1rem}.sign-up-page .submit-btn:hover{cursor:pointer;background-color:#357ae8}

.sign-in-page{width:100%;height:100vh}.sign-in-page .sign-in-container{width:80%;height:80%;background-color:lightgray;-webkit-transform:translate(14%, 11%);transform:translate(14%, 11%);border-radius:2rem;display:flex}.sign-in-page .sign-in-container .sign-in-form{width:30%;margin:0 auto;display:flex;flex-direction:column}.sign-in-page .sign-in-container .sign-in-form h2{text-align:center;margin-top:3rem;font-size:2.5rem}.sign-in-page .sign-in-container .sign-in-form label{margin-top:1.7rem;font-size:0.9rem;font-weight:600;color:gray;opacity:0.8}.sign-in-page .sign-in-container .sign-in-form input{margin-top:0.6rem;height:3rem;background-color:white;border:1px solid black;border-radius:0.5rem;font-size:1rem;letter-spacing:0.1rem;padding-left:1rem;border:none;border:1.5px solid blue}.sign-in-page .sign-in-container .sign-in-form input::-webkit-input-placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-in-page .sign-in-container .sign-in-form input:-ms-input-placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-in-page .sign-in-container .sign-in-form input::placeholder{letter-spacing:normal;font-size:80%;opacity:1}.sign-in-page .sign-in-container .sign-in-form .sign-up-link{color:blue;margin-top:1rem;text-align:end;opacity:0.9}.sign-in-page .sign-in-container .sign-in-form .submit-btn{width:20%;margin:2rem auto 0 auto;padding:0.8rem;border-radius:1.4rem;border:transparent;background-color:#999999;color:white;font-size:0.9rem}.sign-in-page .sign-in-container .sign-in-form .submit-btn:hover{cursor:pointer;background-color:#777777}.sign-in-page .sign-in-container .sign-in-form .btn{width:80%;margin:2.5rem auto 0 auto;padding:0.8rem;border:transparent;border-radius:0.2rem;color:white;font-size:0.9rem}.sign-in-page .sign-in-container .sign-in-form .btn i{margin-right:0.5rem}.sign-in-page .sign-in-container .sign-in-form .btn:hover{cursor:pointer}.sign-in-page .sign-in-container .sign-in-form .google-btn{background-color:#4285f4}.sign-in-page .sign-in-container .sign-in-form .google-btn:hover{background-color:#357ae8}.sign-in-page .sign-in-container .sign-in-form .facebook-btn{background-color:#3b5998}.sign-in-page .sign-in-container .sign-in-form .facebook-btn:hover{background-color:#2f4779}.sign-in-page .sign-in-container .sign-in-detail{width:40%;height:100%;background-image:url(https://i.pinimg.com/originals/a0/74/a8/a074a8a62cf3c17b39c70aa1270ef4eb.jpg);background-repeat:no-repeat;background-position:center;background-size:cover;border-radius:2rem 0 0 2rem;color:white;text-align:center;position:relative}.sign-in-page .sign-in-container .sign-in-detail h2{margin:7rem;font-size:40px}.sign-in-page .sign-in-container .sign-in-detail p{font-weight:500;font-size:1rem;margin-top:5rem;position:absolute;bottom:2rem;left:2rem}.sign-in-page .sign-in-container .sign-in-detail div{padding:0 4rem;letter-spacing:0.1rem}.sign-in-page .sign-in-container .sign-in-detail div strong{color:tomato;font-size:18px}

.spinner{position:absolute;top:45%;left:50%;color:blue}

.profile-container{margin-top:5rem;margin-left:5rem;padding:6rem;font-family:"Jua", sans-serif}.profile-container .profile-img{width:16rem;height:16rem;overflow:hidden;border-radius:50%;cursor:pointer}.profile-container .profile-img .real-input{display:none}.profile-container .user-info{font-size:1.6rem;margin-top:5rem}.profile-container .user-info .user{margin-top:2rem}

*{box-sizing:content-box;margin:0}.menu-item{overflow:hidden;min-width:30%}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.02);transform:scale(1.02);transition:-webkit-transform 400ms ease-out;transition:transform 400ms ease-out;transition:transform 400ms ease-out, -webkit-transform 400ms ease-out;opacity:0.8}.menu-item:hover .background-image .content{background-color:#424242;color:white;opacity:1}.menu-item .background-image{position:relative;height:700px;background-size:cover;background-repeat:no-repeat;background-position:center}.menu-item .background-image .content{position:absolute;width:7rem;height:7rem;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;top:50%;left:50%;background-color:white;opacity:0.7;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#363636;border-radius:0.5rem}.menu-item .background-image .content .title{font-weight:600;margin:0 3px 0;font-size:20px;font-family:"Jua", sans-serif;letter-spacing:0.4rem}

.menu-container{display:flex;margin-left:7rem;margin-top:8.5rem;height:40rem;-ms-overflow-style:none}.menu-container ::-webkit-scrollbar{display:none}.menu-container .menu-items{border-radius:2rem;width:75%;overflow:auto}

.menu-aside{list-style:none;min-width:200px;margin-left:5rem;border-radius:1rem;border:1px solid black;padding-left:0}.menu-aside li{margin-left:0;padding:1.5rem 0 1.5rem 2rem;font-size:1.3rem;border-radius:1rem}.menu-aside li:hover{background-color:#a9a9ff;color:white;cursor:pointer}

.user-profile{width:4.2rem;height:4.2rem;overflow:hidden;background-color:black;border-radius:50%;cursor:pointer}.dropdown-menu{z-index:2}

.header-container{display:flex;position:relative;padding-top:3rem;padding-left:3rem;padding-bottom:1.5rem;border-bottom:1.5px solid #4b4b4b;background-color:#ebebeb;opacity:0.9;z-index:1}.header-container .logo-img{cursor:pointer}.header-container .header-element{display:flex;position:absolute;right:11rem;font-size:1.3rem;line-height:40px;font-weight:600;font-family:"Jua", sans-serif}.header-container .header-element .header-write{text-decoration:none;color:#474242}.header-container .header-element .sign-in-btn{text-decoration:none;color:#474242}.header-container .header-element .header-write{margin-right:2rem}.header-container .header-element .header-photocard{margin-right:2rem;color:#311c4d;text-decoration:none}.header-container .header-element .header-sneakers{margin-right:4rem}

.write-container{height:100vh;display:flex;justify-content:center;align-items:center;margin-top:2.5rem}.write-container .inputs-form{display:flex;flex-direction:column;margin-left:15rem;margin-right:6rem;width:900px;position:relative}.write-container .inputs-form .image-preview{background-repeat:no-repeat;background-position:center;background-size:contain}.write-container .inputs-form .image-preview p{font-family:"Jua" , sans-serif;font-size:1.3rem}.write-container .inputs-form .select-container{display:flex;margin-bottom:2rem}.write-container .inputs-form .select-container label{font-size:1.2rem;font-weight:600;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif}.write-container .inputs-form .select-container .idol-selector{margin-left:2rem;width:15%;display:inline-block}.write-container .inputs-form .head-input{height:6rem;border:none;border-bottom:2px solid black;background-color:#dadae0;opacity:0.8;padding-left:1rem;font-weight:600;font-size:2rem}.write-container .inputs-form .head-input:focus{outline:none}.write-container .inputs-form .main-input{border:none;border-bottom:1px solid black;height:35rem;padding:1rem 0 0 1rem;font-size:1.2rem}.write-container .inputs-form .main-input:focus{outline:none}.write-container .inputs-form .file-input{display:none}.write-container .inputs-form .footer-element{display:flex}.write-container .inputs-form .footer-element .file-btn{width:12%;padding:0.7rem;margin-left:auto;margin-top:1rem;margin-right:2rem;color:white;border:none;font-size:1.2rem;background-color:#5ed464}.write-container .inputs-form .footer-element .file-btn:hover{background-color:#45be4b}.write-container .inputs-form .footer-element .submit-btn{width:12%;padding:0.7rem;margin-top:1rem;color:white;border:none;font-size:1.2rem;background-color:#4285f4}.write-container .inputs-form .footer-element .submit-btn:hover{background-color:#357ae8}

.writing-container{width:100%;height:100vh;overflow-y:auto;overflow-x:hidden;background-color:rgba(207,228,255,0.5);border-radius:1rem;padding-left:2rem;z-index:-2}.writing-container .writing-created-at{margin-top:4rem}

.writing-card{width:95%;height:240px;display:flex;position:relative;padding-left:1rem;padding-top:1rem;padding-bottom:1rem;background-color:#cde1ff;border-radius:1.5rem;margin-top:2rem;margin-left:2rem;box-shadow:4px 4px 7px 2px rgba(0,0,0,0.4);z-index:0}.writing-card:hover{-webkit-transform:scale(1.007);transform:scale(1.007);transition:-webkit-transform 300ms ease-out;transition:transform 300ms ease-out;transition:transform 300ms ease-out, -webkit-transform 300ms ease-out}.writing-card .writing-image{align-self:center}.writing-card .writing-info{margin-top:1rem;margin-left:2rem;font-family:"Jua", sans-serif}.writing-card .delete-btn{position:absolute;right:3rem;bottom:3rem;border-radius:0.5rem;outline:none;border:0.5px solid tomato;color:tomato;background-color:white;padding:0.5rem;font-size:1.2rem;font-weight:300;font-family:"Jua", sans-serif}.writing-card .delete-btn:hover{cursor:pointer;background-color:tomato;border:0.5px solid white;color:white}

